@font-face {
  font-family: 'ComicSans';
  src: url('./assets/fonts/comicz.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

html {
  scrollbar-gutter: stable both-edges;
}


/* Estilo dos links */
a,
a:visited,
a:active,
a:focus {
  color: #ffffff;
  text-decoration: none;
}

/* Corpo da página */
body {
  background-color: black;
  color: white;
  font-family: 'ComicSans';
}

/* Título */
header.titulo {
  text-align: center;
}

header.titulo h1 {
  font-size: 100px;
  font-weight: bold;
}

/* Menu centralizado */
ul.menu {
  text-align: center;
  padding: 0;
  list-style: none;
  margin: 40px 0;
}

/* Ajuste para <li>, mantendo espaçamento */
ul.menu li {
  display: inline-block;
  margin: 0 5px;
}

a.moldura {
  display: inline-block;
  width: 180px;
  height: 80px;
  background-size: contain; /* <-- muda aqui */
  background-repeat: no-repeat;
  background-position: center;
  color: #ffffff;
  text-align: center;
  font-size: 18px;
  line-height: 80px;
  padding: 0 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.text {
  max-width: 800px;
  margin: 40px auto;         /* centraliza horizontalmente */
  padding: 20px;
  text-align: justify;       /* justifica o texto */
  font-size: 18px;
  line-height: 1.6;
  color: white;
}


/* Imagem de capa */
.imagem_capa {
  width: 45%;
  height: 1000px;
  background-size: cover;
  background-position: center;
  display: block;
  margin: 0 auto;
}


